import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const StyledWrapperDiv = styled.div`
  margin-right: 5px;
`;

const StyledDropDownButton = styled.button`
  color: #007bff;
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
`;

const DropDown = ({ onSelect, options }) => {
  const menuItemClick = (e, value) => {
    e.preventDefault();
    onSelect(value);
  };

  const optionItems = options.map((o) => (
    <button
      className="dropdown-item"
      type="button"
      key={o.id}
      onClick={(e) => menuItemClick(e, o.id)}
    >
      {o.label}
    </button>
  ));

  return (
    <StyledWrapperDiv className="dropdown no-print">
      <StyledDropDownButton
        className="dropdown-toggle"
        type="button"
        data-toggle="dropdown"
        aria-expanded="false"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          className="bi bi-pencil-square"
          viewBox="0 0 16 16"
        >
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
          <path
            fillRule="evenodd"
            d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
          />
        </svg>
      </StyledDropDownButton>

      <div className="dropdown-menu">{optionItems}</div>
    </StyledWrapperDiv>
  );
};

DropDown.propTypes = {
  onSelect: PropTypes.func.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.shape({ id: PropTypes.string, label: PropTypes.string })
  ),
};

export default DropDown;
